import './App.css';

import React from "react";
import SimpleMap from './SimpleMap';
import SplitPane from 'react-split-pane';

const locations = require("./locations.json");

class App extends React.Component {

	constructor(props) {
		super(props);
		this.myRef = React.createRef();
	}

	handleMarkerClick(location) {
		if ((undefined !== this.myRef) && (undefined !== this.myRef.current)) {
			this.myRef.current.innerHTML = `Clicked on: <br>`
			+ `<br>`
			+ `id = ${location.id} <br>`
			+ `<br>`
			+ `latitude = ${location.lat} <br>`
			+ `longitude = ${location.lng} <br>`
			+ `<br>`
			+ `street = ${location.street} <br>`
			+ `city = ${location.city} <br>`
			+ `postcode = ${location.postcode} <br>`
			+ `country = ${location.country} <br>`
			+ ``;
		}
	}

	render() {
		return (
			<div className="App">
				<SplitPane split="vertical" minSize={200} defaultSize={200}>
					<div ref={this.myRef} />
					<SimpleMap locations={locations} handleMarkerClick={(location) => this.handleMarkerClick(location)} />
				</SplitPane>
			</div>
		);
	};
}

export default App;